import React, { useEffect, useState } from "react";
import { Redirect, Link } from "react-router-dom";

import { useDispatch, useSelector } from "react-redux";
import { withRouter } from "react-router";
import SKUItem from "Component/SKUItem";
import "./index.scss";
import { selectedSkuCheck, removeSku, preRemoveSku,resetSKU } from "Page/StockEntry/GeneralServices/moduleServices";
import PopUp from "Component/Modal/PopUp";

const RemoveJobSheet = (props) => {
    const dispatch = useDispatch();
    const status = useSelector(state => state.status)
    const stockTransferEntryData = useSelector(state => state.stockTransferEntryData)
    const isCheckedTotal = selectedSkuCheck({ moduleEntry: stockTransferEntryData, moduleName: 'stockTransfer' });
    const [initialJs, setInitialJs] = useState(isCheckedTotal)
    useEffect(() => {
        const initial = selectedSkuCheck({ moduleEntry: stockTransferEntryData, moduleName: 'stockTransfer' });
        setInitialJs(initial)
    }, []);
    const dimention = document.documentElement.clientHeight
    const height = dimention - 210
    const emptyItem = dimention - 210
    if (!stockTransferEntryData) {
        return (
            <div className='d-flex align-items-center justify-content-center text-center w-100 h-screen'>
                <Redirect to='/stockentry/stock-take'>Please Select At Least 1 SKU</Redirect>
            </div >)
    }
    const { list } = stockTransferEntryData?.jobPost
    return (
        <div className="w-100 d-flex flex-column stock-take-summary">
            <PopUp status={status} />
            <div className="pt-3 pb-0 pl-3 pr-3 bg-white border-all radius-all mb-3">
                <div className="d-md-flex d-lg-flex mb-3 justify-content-between">
                    <div className="text LatoBold d-flex justify-content-between align-items-center sku-des">
                        <div>
                            {`Check SKU${list.length > 1 ? 's' : ''} that you want to remove from entries`}
                            <span className="green">{`${initialJs - isCheckedTotal > 0 ? (` (${initialJs - isCheckedTotal} Removed Sku${((initialJs - isCheckedTotal) > 1 ?  's' : '' )})`) : ''}`}</span>
                        </div>
                        <div className="icon-unfinishEn">
                            <i className="iconU-sideBarInventoryEntry emptyTextField"></i>
                        </div>
                    </div>
                </div>
                <div
                    className={`mb-1 mt-3 d-flex align-items-center scroll-sku ${list?.length ? "flex-column" : "flex-row"
                        } w-100 text-center`}
                    style={{ minHeight: height, maxHeight: height }} >
                    {list?.length ? (
                        list?.map((data, idx) => {
                            return (
                                <SKUItem
                                    key={idx}
                                    img={data?.photo}
                                    skuDescription={data?.skuName}
                                    skuNumber={data?.skuNumber}
                                    uomLabel="weight"
                                    uomValue={data?.totalQty}
                                    uom={data?.unitOfMeassures}
                                    isCheckedHandler={(checked) =>
                                        preRemoveSku({ idx, checked: !checked, dispatch, moduleEntry: stockTransferEntryData, moduleName: 'stockTransfer' })
                                    }
                                    check={!data?.checked}
                                />
                            );
                        })
                    ) : (
                            <div className="w-100 text-center emptyTextField" style={{ minHeight: emptyItem }}>
                                "No Data"
                            </div>
                        )}
                </div>
            </div>
            <div className="h-screen-summary-footer d-flex flex-column justify-content-start">
                <div className="w-100 d-flex align-items-center justify-content-between mb-1">
                    <Link to='/inventory-entry/stock-transfer/weight-entry'>
                        <button
                            onClick={() => resetSKU({dispatch, moduleSummary:stockTransferEntryData,moduleName:'stockTransfer'})}
                            type="button"
                            className="btn px-4 no-shadow border-all bg-white fontSizeButton"
                        >
                            Cancel
                    </button>
                    </Link>

                    <Link to='/inventory-entry/stock-transfer/weight-entry' onClick={(e) => isCheckedTotal !== initialJs ? true : e.preventDefault()}>
                        <button
                            onClick={() => isCheckedTotal !== initialJs ? removeSku({ dispatch, moduleEntry: stockTransferEntryData, moduleName: 'stockTransfer' }) : null}
                            type="button"
                            className={`btn px-5 fontSizeButton no-shadow text-white ${isCheckedTotal !== initialJs ? "bg-green" : "bg-lineGrey"}`}
                        >
                            Remove
                    </button>
                    </Link>
                </div>
            </div>
        </div>
    );
};

export default withRouter(RemoveJobSheet);